.navbar-styling{background-color:#23A3D8}.navbar-styling a{color:white !important;opacity:0.8}.navbar-styling a:hover{color:white;opacity:1}.navbar-styling .navbar-toggler{border-color:#fff !important;color:white}.navbar-styling .title-styling{white-space:pre-wrap;text-align:center;line-height:20px}

.bg-blue{background-color:#23A3D8}.bg-blue h1,.bg-blue p{color:white}.bg-blue p{font-size:20px;margin:0}

html{scroll-behavior:smooth}.base{font-family:"Ubuntu",sans-serif}.b-700{font-weight:700}.f-15{font-size:15px}.row{margin-right:0px !important;margin-left:0px !important}@media screen and (max-width: 575.98px){p{font-size:14px}h1{font-size:32px !important}h2{font-size:28px !important}}

.card{border-radius:20px !important;box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07);-webkit-box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07);-moz-box-shadow:0px -1px 11px 8px rgba(0,0,0,0.07)}.card .image-size{color:#23A3D8;width:7em;height:auto}.card ul{padding:0;list-style-type:none;font-size:24px}.card .stack{border-bottom:3px solid #23A3D8}.card:hover{box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-webkit-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-moz-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);transition:.3s ease}@media screen and (max-width: 575.98px){.card .image-size{width:5em;height:auto}}

.project-base-card{cursor:pointer}.project-card{border-radius:15px}.project-card__container{position:relative;text-align:center}.project-card__img{max-width:100%;height:50%;border-radius:15px}.project-card__title{position:absolute;width:100%;height:100%;top:0;left:0;background-color:rgba(35,162,216,0.541);transition:.3s ease;opacity:0}.project-card__title-position{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%)}.project-card h2{color:white}.project-card__container:hover .project-card__title{opacity:1}.modal-image{cursor:pointer;border:1px solid #23A3D8;border-radius:10px}.modal-image:hover{cursor:pointer;box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-webkit-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);-moz-box-shadow:0px -1px 11px 8px rgba(35,163,216,0.21);transition:.3s ease}.features{white-space:pre-wrap}.main-image{max-width:80%;height:auto;align-items:center}

.social-media-icon{display:flex;justify-content:center;align-items:center}.social-media-icon__container{display:flex;justify-content:center;align-items:center;border:2px solid #23A3D8;border-radius:50%;padding:10px;width:60px;height:60px;margin:0px 10px;font-size:2rem;color:#23A3D8}.social-media-icon__container:hover{background-color:#23A3D8;color:white;transition:0.2s ease-in-out}@media screen and (max-width: 575.98px){.social-media-icon__container{padding:5px;width:45px;height:45px;border:1px solid #23A3D8;margin:0px 10px;font-size:1.5rem;color:#23A3D8}}

